.containerHome {
  text-align: center;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-top: 30px;
}

.headingHome {
  font-size: 28px;
  font-weight: bold;
  color: #333;
  margin-bottom: 15px;
}

.descriptionHome {
  font-size: 16px;
  color: #555;
  margin-bottom: 30px;
}

.containerRegister,
.containerLogin {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  background-color: #fff;
  margin-top: 20px;
}

.titleRegister,
.titleLogin {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}

.formRegister,
.formLogin {
  display: flex;
  flex-direction: column;
}

.labelRegister,
.labelLogin {
  margin-bottom: 15px;
}

.inputRegister,
.inputLogin {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.buttonRegister,
.buttonLogin {
  background-color: #007bff;
  color: #fff;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 20%;
  margin: 0 auto;
  margin-top: 10px;
}

.buttonRegister:hover,
.buttonLogin:hover {
  background-color: #0056b3;
}
